<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col cols="12" class="col-lg-12 col-sm-12">
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p class="ghx-stepper-content__question text--primary mb-0 pb-8" v-html="questionItem.text">
            </p>
          </v-card-text>
          <v-row>
            <v-col cols="12" lg="6" md="6">
              <v-card-text class="ghx-subtitle text-left pt-0 px-0 pb-4">
                <p class="text-subtitle-1 font-weight-light">
                  {{ questionItem.message }}
                </p>
              </v-card-text>
            </v-col>
            <v-col cols="12" lg="6" md="6">
              <v-card-text class="pa-0 ghx-form">
                <v-row class="my-0">
                  <v-col cols="12" class="pb-0 pt-1">
                    <v-text-field
                      v-model="form.email"
                      class="ghx-form-input"
                      label="Email"
                      :rules="[rules.required, rules.email]"
                      outlined
                      @keyup.enter="$emit('on-hit-enter')"></v-text-field>
                  </v-col>
                  <v-col cols="12" class="pb-0 pt-1">
                    <v-text-field
                      v-model="form.first_name"
                      class="ghx-form-input"
                      label="First Name"
                      :rules="[rules.required]"
                      outlined
                      @keyup.enter="$emit('on-hit-enter')"></v-text-field>
                  </v-col>
                  <v-col cols="12" class="pb-0 pt-1">
                    <v-text-field
                      v-model="form.last_name"
                      class="ghx-form-input"
                      label="Last Name"
                      :rules="[rules.required]"
                      outlined
                      @keyup.enter="$emit('on-hit-enter')"></v-text-field>
                  </v-col>
                  <v-col cols="12" class="pb-0 pt-1">
                    <v-text-field
                      v-model="form.phone_number"
                      class="ghx-form-input"
                      label="Phone Number"
                      :rules="[rules.phone_number]"
                      outlined
                      @keyup.enter="$emit('on-hit-enter')"></v-text-field>
                  </v-col>
                  <v-col cols="12" class="pb-0 pt-1">
                    <v-text-field
                      v-model="form.zip_code"
                      class="ghx-form-input"
                      label="Zip Code"
                      :rules="[rules.zip_code]"
                      outlined
                      @keyup.enter="$emit('on-hit-enter')"></v-text-field>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { questionnaire } from '@/store/main'
  const PHONE_NUMBER_LENGTH = 14
  const ZIP_CODE_NUMBER_LENGTH = 6

  export default {
    name: 'StepFourPersonal',
    setup() {},
    props: {
      questionItem: {
        type: Object,
        default: () => {}
      },
      
      currentOrder: {
        type: Number,
        default: 0
      }
    },
    computed: {
      isEmptyForm () {
        return this.emptyForms.length > 0 ? true : false
      },
      emptyForms () {
        var hasEmptyObj = []
        Object.keys(this.form).forEach((i) => {
          if (this.form[i] === '') {
            hasEmptyObj.push(i)
          }
        })
        return hasEmptyObj
        //return this.form.email  === ''
      },
      validEmail () {
        return this.rules.email(this.form.email) === true ? true : false
      },
      validPhoneNumber () {
        return this.form.phone_number.length === PHONE_NUMBER_LENGTH
      },
      validZipCode () {
        return this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH || this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH - 1
      },
      storeForm() {
        return this.store.getForm
      }
    },
    
    watch: {
      'isEmptyForm': {
        handler (val) {
          if (!val && this.validEmail && this.validPhoneNumber && this.validZipCode) {
            var resource = {
              selected: this.form,
              resource: this.questionItem
            }
            this.$emit('on-item-selected', resource)
          } else {
            this.$emit('on-item-emptied')
          }
        },
        deep: true
      },
      
      currentOrder (val) {
        if (val === this.questionItem.order && this.validEmail && this.validPhoneNumber && this.validZipCode) {
          
          var resource = {
            selected: this.form,
            resource: this.questionItem
          }
          
          this.$emit('on-item-selected', resource)
        }
      },
      
      form: {
        handler (val) {
          this.store.updateForm(val)
          this.skip = true
        },
        deep: true
      },
      
      storeForm(val) {
        if (this.skip) {
          this.skip = false
          return
        }
        
        
        this.form = {
          ...this.form,
          ...val
        }
      }
    },
    
    data() {
      return {
        el: 1,
        dob_menu: false,
        selectedItem: null,
        form: {
          first_name: '',
          last_name: '',
          email: '',
          phone_number: '',
          zip_code: ''
        },
        rules: {
          required: value => !!value || 'This field is required.',
          phone_number: value => {
            var x = value.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
            this.form.phone_number = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '');
            return this.form.phone_number.length === PHONE_NUMBER_LENGTH ? true : 'Invalid phone number.'
          },
          email: value => {
            const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            return pattern.test(value) || 'Invalid e-mail.'
          },
          zip_code: value => {
            var upcase = value.toUpperCase();
            var x = upcase.replace(/[^a-zA-Z0-9]/g, '').match(/([a-zA-Z0-9_-]{0,6})/);
            this.form.zip_code = x[1];
            return this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH || this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH - 1 ? true : 'Invalid zip code.'
          }
        },
        store: questionnaire(),
        skip: false
      }
    }
  }
</script>

