                      @on-item-emptied="onEmptyItem()" />
                    <StepOneV2
                      v-else
                      :question-item="item"
                      :initializer="item.order === 1"
                      :currentOrder="el"
                      @initialized="onInitialization"
                      @on-item-selected="onSelectItem"
                      @on-item-emptied="onEmptyItem()" />
                  </div>
  
                  <div v-if="item.step === 2" class="pt-12 pb-12">
                    <StepTwo
