                        class="ghx-btn ghx-btn--secondary px-4 col-xs-hide col-sm-hide">
                          Save My Spot and Exit
                      </v-btn>
                      
                      <v-btn
                        @click="onSaveSpotForm(false)"
                        small depressed fab plain
                        color="#6548c6"
                        height="40"
                        class="ghx-btn ma-0 pa-0 col-md-hide col-lg-hide">
                        <v-icon>mdi-content-save</v-icon>
                      </v-btn>
                      
                      <v-spacer></v-spacer>
                      
                      <v-btn
                        v-if="el > 1"
                        @click="onPrevious"
                        large rounded depressed text
                        color="undefined"
                        height="40"
                        class="ghx-btn ghx-btn--secondary px-4 pr-6">
                        <v-icon right >mdi-arrow-left</v-icon>
