                        class="ghx-btn ghx-btn--primary px-4">
                        Get My Results
                      </v-btn>
                      
                      <v-btn
                        v-else-if="excludedBtns?.indexOf(el) < 0"
                        id="ghx-btn--continue"
                        :loading="buttonLoading"
                        @click="onClickContinue(false)"
                        :disabled="!selectedItem"
                        large rounded depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary px-4 pl-6">
                        Continue
                        <v-icon right >mdi-arrow-right</v-icon>
                      </v-btn>
                      
                      <v-btn
                        v-else
                        id="ghx-btn--continue"
                        @click="onClickContinue(true)"
                        :loading="buttonLoading"
                        large rounded depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary px-4 pl-6">
                        Continue
                        <v-icon right >mdi-arrow-right</v-icon>
                      </v-btn>
                  </v-card-actions>
                </v-col>
              </v-card>
            </v-stepper-items>
            
            <v-stepper-items v-else-if="isShowFinalPage">
              <!--<DisplayResult :selectedQuestion="selectedQuestion" :selectedFlow="selectedFlow"/>-->
              <v-col col="12" class="pa-0 ghx-stepper-content">
                <ul>
                  <li v-for="(value, name) in finalResult.data" :key="name">
                    {{name}}:
                    <span v-if="typeof value === 'object'">
                      <list-comp :item="value"></list-comp>
                    </span>
                    <span v-else> {{value}} </span>
                  </li>
                </ul>
              </v-col>

              <v-card class="ghx-stepper__action ghx-glass-background pa-0 ml-1 mr-1 d-flex">
                <v-col col="12" class="col-lg-3 col-sm-hide d-flex pa-0">
                  <v-spacer></v-spacer>
                </v-col>
                <v-col col="12" class="col-lg-9 col-sm-12 ghx-action-btn-container d-flex pa-0">
                  <v-card-actions class="ghx-stepper__action-wrapper pt-4 pb-0">
                    <v-spacer></v-spacer>
                    <v-btn
                      @click="getResult"
                      large rounded depressed
                      color="undefined"
                      height="50"
                      class="ghx-btn ghx-btn--primary px-4">
                      Get My Results
                    </v-btn>
                  </v-card-actions>
                </v-col>
              </v-card>
            </v-stepper-items>
            
            <!--<v-stepper-items v-show="isShowSaveFormPage">
              <v-stepper-content>
                <SaveAndExit />
              </v-stepper-content>

              <v-card class="ghx-stepper__action ghx-glass-background pa-0 ml-1 mr-1 d-flex">
                <v-col col="12" class="col-lg-3 col-sm-hide d-flex pa-0">
                  <v-spacer></v-spacer>
                </v-col>
                <v-col col="12" class="col-lg-9 col-sm-12 ghx-action-btn-container d-flex pa-0">
                  <v-card-actions class="ghx-stepper__action-wrapper pt-4 pb-0">
                      <v-spacer></v-spacer>
                      <v-btn
                        large rounded depressed
                        color="#C5D697"
                        height="40"
                        class="ghx-btn px-4 pr-6">
                        Save and Exit
                      </v-btn>
                      <v-btn
                        @click="onContinueSteps"
                        large rounded depressed text
                        color="grey"
                        height="40"
                        class="ghx-btn px-4 pl-6">
                        Continue
                        <v-icon right >mdi-arrow-right</v-icon>
                      </v-btn>
                  </v-card-actions>
                </v-col>
              </v-card>
            </v-stepper-items>-->

          </v-stepper>
          <v-stepper class="ghx-stepper elevation-0" alt-labels v-show="isShowSaveFormPage">
            <v-stepper-items>
              <div class="ghx-stepper-content">
                <div class="v-stepper__wrapper">
                  <div class="pb-12 pt-12">
                    <SaveAndExit 
                      :isShow="isShowSaveFormPage"
                      @on-reset="$emit('on-reset')"
                      @on-item-selected="onFilledForm"
                      @on-item-emptied="onEmptyForm()"
                    />
                  </div>
                </div>
              </div>

              <v-card class="ghx-stepper__action ghx-glass-background pa-0 ml-1 mr-1 d-flex">
                <v-col col="12" class="col-lg-3 col-sm-hide d-flex pa-0">
                  <v-spacer></v-spacer>
                </v-col>
                <v-col col="12" class="col-lg-9 col-sm-12 ghx-action-btn-container d-flex pa-0">
                  <v-card-actions class="ghx-stepper__action-wrapper pt-4 pb-0">
                    <v-spacer></v-spacer>
                    <template
                      v-if="isExitClicked">
                    
                      <v-btn
                        @click="onContinueSteps"
                        large rounded depressed text
                        color="undefined"
                        height="40"
                        class="ghx-btn ghx-btn--secondary px-4 col-xs-hide col-sm-hide">
                        <v-icon right >mdi-arrow-left</v-icon>
                        Back
                      </v-btn>
                      
                      <v-btn
                        @click="onClickSaveSpot"
                        :loading="saveSpotBtnLoading"
                        :disabled="!saveSpotForm"
                        large rounded depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary px-4 col-xs-hide col-sm-hide">
                          Save My Spot and Exit
                      </v-btn>
                      
                      <v-btn
                        @click="onContinueSteps"
                        small depressed fab plain
                        color="#6548c6"
                        height="40"
                        class="ghx-btn px-4 col-md-hide col-lg-hide mx-4">
                        <v-icon right >mdi-arrow-left</v-icon>
                      </v-btn>
                      
                      <v-btn
                        @click="onClickSaveSpot"
                        :loading="saveSpotBtnLoading"
                        :disabled="!saveSpotForm"
                        small depressed fab plain
                        color="#6548c6"
                        height="40"
                        class="ghx-btn px-4 col-md-hide col-lg-hide ml-2 mr-4 no-bg">
                          <v-icon>mdi-content-save</v-icon>
                      </v-btn>
                      
                      <v-btn
                        @click="resetAndClose"
                        large rounded depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary px-4"
                        :class="saveSpotForm ? 'v-btn--disabled': ''">
                        Exit
                      </v-btn>
                      
                    </template>
                    <template
                      v-else>
                      <v-btn
                        @click="onContinueSteps"
                        large rounded depressed text
                        color="undefined"
                        height="40"
                        class="ghx-btn ghx-btn--secondary px-4">
                        <v-icon right >mdi-arrow-left</v-icon>
                        Back
                      </v-btn>
                      
                      <v-btn
                        @click="onClickSaveSpot"
                        :loading="saveSpotBtnLoading"
                        :disabled="!saveSpotForm"
                        large rounded depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary px-4">
                          Save My Spot and Exit
