</template>

<script>

import {
  PERSONAL,
  GENERAL,
  DEFAULT_PERSONAL_QUESTIONS,
  DEFAULT_GENERAL_QUESTIONS } from '@/constants/questions'
import StepOne from './Steppers/PersonalStepOne.vue'
import StepTwo from './Steppers/PersonalStepTwo.vue'
import StepThree from './Steppers/PersonalStepThree.vue'
import StepFour from './Steppers/PersonalStepFour.vue'
import StepOneV2 from './Steppers/GeneralStepOne.vue'
import StepTwoV2 from './Steppers/GeneralStepTwo.vue'
import StepThreeV2 from './Steppers/GeneralStepThree.vue'
import SaveAndExit from './SaveAndExit.vue'
import DisplayResult from './DisplayResult.vue'

import Audigy from '@/models/Audigy'
import { questionnaire } from '@/store/main'

import { ref, watch, computed, onMounted, nextTick } from 'vue'
import Util from '@/helpers/Util'

export default {
  setup () {
    const store = questionnaire()
    const el = ref(0)
    const lastPage = ref(0)
    const selectedItem = ref(null)
    const saveSpotForm = ref(null)
    const forceContinue = ref(false)
    const excludedBtns = ref([8, 9, 10, 15])
    const selectedFlow = ref(PERSONAL)
    const isShowSaveForm = ref(false)
    const skipInitExperiment = ref(false)
    const isExitClicked = ref(false)
    
    const flowSelector = computed(() => {
      if (selectedFlow.value === GENERAL) {
        return DEFAULT_GENERAL_QUESTIONS
      }
      return DEFAULT_PERSONAL_QUESTIONS
    })

    watch(el, async (currentValue) => {
      switch (currentValue) {
        case 8:
        case 9:
        case 10:
        case 15:
          forceContinue.value = true
          break
        default:
          forceContinue.value = false
      }
      
      onSetStepIndicatorWidth(currentValue)
      
    });
    
    const concatText = (args) => {
      var str = ''
      args.forEach(item => {
        str = `${str}${item}`
      })
      
      return str;
    }

    // Methods
    const onSelectItem = (data) => {
      selectedItem.value = data
    }

    const onEmptyItem = () => {
      selectedItem.value = null
    }

    const onEmptyForm = () => {
      saveSpotForm.value = null
    }

    const onFilledForm = (data) => {
      saveSpotForm.value = data
    }

    const onContinue = () => {
      var selectedItemValue = selectedItem.value;
      var meta = selectedItemValue.resource?.form?.meta === 'final' ? selectedItemValue.resource?.form?.meta: concatText([selectedItemValue.resource.form.meta, selectedItemValue.resource.form.hz ? selectedItemValue.resource.form.hz: '']);
      skipInitExperiment.value = false;
      
      if (meta === 'date_of_birth') {
        var prevVal = store.getUserResponse[meta];
        
        skipInitExperiment.value = selectedItemValue.selected === prevVal;
      }
      
      store.addUserResponse({
        field: meta,
        value: selectedItemValue.selected
      })
    }

    const onContinueScreener = () => {
      if (!forceContinue.value) {
        store.addUserResponse({
          field: concatText([selectedItem.value.resource.form.meta, selectedItem.value.resource.form.hz ? selectedItem.value.resource.form.hz: '']),
          value: selectedItem.value.selected
        })
      }
    }

    const onPrevious = () => {
      el.value --
      
      var prevItem = flowSelector.value.find( item => item.order === el.value )
      
      selectedItem.value = {
        selected: {label: store?.getUserResponse[prevItem?.form?.meta + prevItem?.form?.hz ? prevItem?.form?.hz: '']},
        resource: prevItem
      }
    }
    
    const onSaveSpotForm = (isExit = true) => {
      isShowSaveForm.value = true;
      isExitClicked.value = isExit;
      
    }
    
    const onContinueSteps = () => {
      isShowSaveForm.value = false;
      isExitClicked.value = false;
    }

    const onInitialization = (data) => {
      selectedFlow.value = data === 'Loved One' ? GENERAL : PERSONAL
    }
    
    const onGetTotalItems = (key, value, items) => {
      return items.filter(item => item[key] === value);
    }
    
    const onIntStepIndicator = async (data) => {
      await nextTick()

      var steps = document.querySelectorAll('.ghx-stepper-step');
      
      steps.forEach((stp, i) => {
        var isLastLine = i === steps.length - 1;
        var divider = stp.querySelector('.ghx-divider')
        var nextIndex = isLastLine ? 0: i + 1;
        var nextSibling = steps[nextIndex];

        var startLine = (stp.clientWidth / 2) + stp.offsetLeft;
        var endLine = (nextSibling.clientWidth / 2) + nextSibling.offsetLeft;
        
        var lineWidth = Math.abs(endLine - startLine);
        
        if (isLastLine) 
          divider.style.width = lineWidth + "px";
        else 
          divider.setAttribute('data-step-width', (lineWidth / parseInt(divider.getAttribute('data-items'))))
      })
    }
    
    const onSetStepIndicatorWidth = async (currentValue) => {
      if (flowSelector.value.length === currentValue - 1)
        return
        
      var data = flowSelector.value.find(item => item.order === currentValue);
      var step = data.step;
      var stepItems = onGetTotalItems('step', step, flowSelector.value);
      var orderIndex = stepItems.findIndex(item => item.order === currentValue);
      
      if (step > 1 && !orderIndex) {
        var prevDivider = document.querySelector('.ghx-stepper-step' + (step - 1) + ' .ghx-divider');
        prevDivider.style.width = (parseInt(prevDivider.getAttribute('data-step-width')) * parseInt(prevDivider.getAttribute('data-items'))) + "px";
      }
      
      var stepElems = document.querySelectorAll('.ghx-stepper-step');
      
      stepElems.forEach(stepElem => {
        var divider = stepElem.querySelector('.ghx-divider');
        divider.classList.remove('active');
        
        if (stepElem.classList.contains('ghx-stepper-step' + step)) {
          divider.classList.add('active');
          divider.style.width = (parseInt(divider.getAttribute('data-step-width')) * orderIndex) + "px";
        }
      });
    }
      
    const onResize = async () => {
      onIntStepIndicator(flowSelector.value)
      onSetStepIndicatorWidth(el.value)
    }

    return {
      forceContinue,
      store,
      el,
      lastPage,
      selectedItem,
      saveSpotForm,
      flowSelector,
      onSelectItem,
      onEmptyItem,
      onEmptyForm,
      onFilledForm,
      onContinue,
      onContinueScreener,
      onPrevious,
      //onSaveSpot,
      onSaveSpotForm,
      onContinueSteps,
      onInitialization,
      onGetTotalItems,
      onIntStepIndicator,
      onSetStepIndicatorWidth,
      onResize,
      excludedBtns,
      selectedFlow,
      isShowSaveForm,
      skipInitExperiment,
      isExitClicked
    }
  },
  
  props: {
    defaultLastPage: {
      type: Number,
      default: 1
    }
  },
  
  data() {
    return {
      finalResult: null,
      buttonLoading: false,
      saveSpotBtnLoading: false
    }
  },
  
  components: {
    StepOne,
    StepTwo,
    StepThree,
    StepFour,
    StepOneV2,
    StepTwoV2,
    StepThreeV2,
    SaveAndExit,
    //DisplayResult
  },
  
  computed: {
    personalQuestions () {
      return DEFAULT_PERSONAL_QUESTIONS
    },
    generalQuestions () {
      return DEFAULT_GENERAL_QUESTIONS
    },
    general () {
      return GENERAL
    },
    personal () {
      return PERSONAL
    },
    
    isShowSaveFormPage() {
      return this.isShowSaveForm;
    },
    
    selectedQuestion() {
      if (this.selectedFlow === this.personal)
        return this.personalQuestions
      else
        return this.generalQuestions
    },
    
    isFinalPage() {
      var question = this.selectedQuestion.find(item => item.order === this.el);
      return question?.form?.meta === 'final'
    },
    
    isShowFinalPage() {
      const params = Util.getUrlParams();
        
      return params?.testing === 'true';
    },
    
    getUrlParams() {
      return Util.getUrlParams();
    }
  },
  
  watch: {
    flowSelector(currentValue) {
      this.onIntStepIndicator(currentValue)
    },
  },
  
  beforeDestroy() {
    window.removeEventListener('resize', this.onResize, {passive: true})
    window.removeEventListener('keypress', this.keypress);
  },

  mounted() {
    window.addEventListener('resize', this.onResize, {passive: true})
    this.el = parseInt(this.defaultLastPage)
    this.lastPage = this.el
    this.onIntStepIndicator(this.flowSelector)
    window.addEventListener('keypress', this.keypress);
  },
  
  methods: {
    async onClickContinue(skip = false) {
      this.buttonLoading = true;
      this.$emit('on-show-loader'); 
      
      if (skip)
        this.onContinueScreener();
      else
        this.onContinue();
      
      if (this.selectedItem && this.selectedFlow === 1 && !this.skipInitExperiment) {
        await Audigy.saveExperimentData(
          '', 
          this.selectedItem.resource.form.meta, 
          this.selectedItem.resource?.form?.hz ? this.selectedItem.resource.form.hz: '', 
          this.selectedItem.resource?.form?.ear ? this.selectedItem.resource.form.ear: '',
        )
      }
      
      
      if (this.isFinalPage) {
        this.finalResult = await this.saveMySpotAndLP(null, true);

        if(!this.isShowFinalPage) {
          this.getResult();
          return;
        }
        
      }
      
      this.el++;
      
      if (this.lastPage < this.el)
        this.lastPage++;
      
      this.selectedItem = null;
      this.buttonLoading = false;
      this.$emit('on-show-loader'); 
    },
    
    async onSaveSpot(id, link, isResultPage, finalResult = null) {
      if (!this.saveSpotForm && !isResultPage)
        return
        
      var response = await Audigy.saveSpotForLater(
        !isResultPage ? this.saveSpotForm?.selected?.email: this.store?.getUserResponse?.final?.email,
        !isResultPage ? this.saveSpotForm?.selected?.first_name: this.store?.getUserResponse?.final?.first_name,
        id, 
        this.selectedQuestion, 
        this.lastPage, 
        this.selectedFlow,
        isResultPage,
        link,
        finalResult
      );
    },
    
    async saveMySpotAndLP(form, isResultPage = false) {
      var id = this.getUrlParams?.savedSpot || Audigy.generateWPId();
      var finalResult = await Audigy.saveFinalData(id, this.selectedQuestion, form, isResultPage ? 'GetMyResults': 'SaveMySpot');

      var link = '';
      
      if (isResultPage)
        link = window.location.origin + "/guided-hearing-experience/results?guid=" + id ;
      else
        link = window.location.origin + "?savedSpot=" + id;
        
      await this.onSaveSpot(id, link, isResultPage, finalResult); 
      
      return finalResult;
    },
    
    async onClickSaveSpot() {
      this.saveSpotBtnLoading = true;
      await this.saveMySpotAndLP(this.saveSpotForm?.selected); 
      this.resetAndClose();
      this.saveSpotBtnLoading = false;
    },
    
    resetAndClose() {
      this.$emit('on-reset'); 
      this.$emit('on-close');
      //Util.deleteUrlParams('savedSpot');
    },
    
    getResult() {
      var dataId = this.getUrlParams?.savedSpot || this.finalResult.data.id || '';
      var flow = "loved-one";
      //var queryStrings = this.getFinalResultRequiredData(this.finalResult);
      
      if (this.selectedFlow === this.personal)
        flow = "for-me";
        
      window.location =  `${window.location.origin}/guided-hearing-experience/results/${flow}?guid=${dataId}`; //&${queryStrings}
    },
    
    // getFinalResultRequiredData(finalResult, covertObject = true) {
    //   var retVal = {};
      
    //   var screener = finalResult?.data?.screenerResults?.result[0];
      
    //   if (screener?.Left?.analyze)
    //     retVal.left = screener?.Left?.analyze;
        
    //   if (screener?.Right?.analyze)
    //     retVal.right = screener?.Right?.analyze;
        
    //   retVal.postalcode = finalResult?.data?.postalCode;
      
    //   return covertObject ? Util.convertUrlParams(retVal): retVal;
    // },
    
    onClickExit() {
      if (this.isExitClicked)
        this.resetAndClose();
      else
        this.onSaveSpotForm();
    },
    
    keypress(e) {
      if (e.target.classList.contains('ghx-btn-play-song'))
        e.preventDefault();
        
      var modals = document.querySelectorAll('.menu-tick-' + this.el);
      modals.forEach(item => {
        item.style.display = 'none';
      })
        
      if (e.key === 'Enter') 
        this.onHitEnter();
    },
    
    onHitEnter() {
      var excludedBtn = this.excludedBtns?.indexOf(this.el) < 0;
      if (this.isShowSaveFormPage && this.saveSpotForm)
        this.onClickSaveSpot();
      else if (this.selectedItem || !excludedBtn)
        this.onClickContinue(!excludedBtn)
    }
  }

}
</script>

