<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col cols="12" class="col-lg-12 col-sm-12">
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p class="ghx-stepper-content__question text--primary" v-html="questionItem.text">
            </p>
          </v-card-text>
          <v-card-text class="text-center">
            <v-item-group 
              :key="key"
              :multiple="questionItem.form.multi_select ? true: false" 
              active-class="primary"
              :value="hasSelectedItem ? hasSelectedItem: questionItem.form.multi_select ? []: ''">
              <div class="ghx-question-selector-wrapper">
                <div
                  v-for="(item, nkey) in questionItem?.form.options"
                  :key="nkey"
                  cols="12"
                  md="4"
                  class="ghx-question-selector">
                  <v-item v-slot="{ active, toggle }" :value="item.label">
                    <v-card
                      class="ghx-question-selector-item d-flex align-center ma-1 py-0 px-6"
                      :class="[{ 'active': active }, item.customClass]"
                      :size="questionItem.form?.button_size"
                      @click="onSelectedItem(item.label, active, item.customClass); toggle()">
                      <v-scroll-y-transition>
                        <div
                          class="ghx-option-item flex-grow-1 text-center">
                          {{ item.label }}
                        </div>
                      </v-scroll-y-transition>
                    </v-card>
                  </v-item>
                </div>
              </div>
            </v-item-group>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { questionnaire } from '@/store/main'
  import Util from '@/helpers/Util'

  export default {
    name: 'StepTwoGeneral',
    
    setup() {
      const store = questionnaire()

      return {
        store
      }
    },
    
    props: {
      questionItem: {
        type: Object,
        default: () => {}
      },
      
      currentOrder: {
        type: Number,
        default: 0
      }
    },
    
    mounted() {
      var temp = this.store?.getSelectedFlow[this.questionItem.form.meta] || this.store.getUserResponse[this.questionItem.form.meta] || null;
      this.hasSelectedItem = Util.filterSelectedDefaultValue(temp, this.questionItem.form);
      
      if (this.hasSelectedItem)
        this.selectedItem = this.hasSelectedItem;
    },
    
    methods: {
      onSelectedItem (data, isActive, reset) {
        if (reset) {
          if (this.questionItem.form.multi_select) {
            if (isActive) {
              var index = this.multiSelectedItem.findIndex( val => val === data)
              this.multiSelectedItem.splice(index, 1);
            }
            else {
              this.multiSelectedItem = [];
              this.multiSelectedItem.push(data)
            }
            
            this.hasSelectedItem = this.multiSelectedItem;
          }
          else 
            this.hasSelectedItem = isActive ? null : data
            
          if (!isActive) {
            this.selectedItem = null;
            this.key = Date.now();
          }
          
          this.selectedItem = this.hasSelectedItem.length ? this.hasSelectedItem: null;
          
          return
        } 
        
        if (this.questionItem.form.multi_select) {
          if (isActive) {
            var index = this.multiSelectedItem.findIndex( val => val === data)
            this.multiSelectedItem.splice(index, 1);
          }
          else
            this.multiSelectedItem.push(data)
  
          this.selectedItem = this.multiSelectedItem.length ? this.multiSelectedItem: null;
        }
        else
          this.selectedItem = isActive ? null : data
          
        if (!reset) {
          var index = this.multiSelectedItem.findIndex( val => val === 'None of the Above')
          
          if (index > -1) {
            if (this.questionItem.form.multi_select) {
              this.multiSelectedItem.splice(index, 1);
              this.hasSelectedItem = this.multiSelectedItem;
            }
              
            this.hasSelectedItem = this.selectedItem;
            this.selectedItem = null;
            this.key = Date.now();
            this.selectedItem = this.hasSelectedItem;
          }
        }
      }
    },
    
    watch: {
      'selectedItem': {
        handler (val) {
          if (val) {
            var resource = {
              selected: val,
              resource: this.questionItem
            }
            
            this.$emit('on-item-selected', resource)
            
            this.store.setSelectedFlow({key: this.questionItem.form.meta, value: val});
          } else {
            this.$emit('on-item-emptied')
          }
        },
        deep: true
      },
      
      currentOrder (val) {
        if (val === this.questionItem.order && this.selectedItem) {
          var resource = {
            selected: this.selectedItem,
            resource: this.questionItem
          }
          
          this.$emit('on-item-selected', resource)
        }
      }
    },
    
    data() {
      return {
        el: 1,
        key: Date.now(),
        dob_menu: false,
        selectedItem: null,
        multiSelectedItem: [],
        hasSelectedItem: null
      }
    }
  }
</script>

