<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col 
        cols="12" 
        class="col-lg-12 col-sm-12"
      >
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p class="ghx-stepper-content__question text--primary" v-html="questionItem.text">
            </p>
          </v-card-text>
          <v-card-text class="text-center" v-if="questionItem.form.type != 'date'" >

            <v-item-group 
              :key="questionItem.form.meta + questionItem.form.type" 
              active-class="primary" 
              :value="hasSelectedItem">
              <div class="ghx-question-selector-wrapper">
                <div
                  v-for="(item, nkey) in questionItem?.form.options"
                  :key="nkey"
                  cols="12"
                  md="4"
                  class="ghx-question-selector">
<!--item.label === store?.getSelectedFlow?.label-->
                  <v-item v-slot="{ active, toggle }" :value="item.label">
                    <v-card
                      class="ghx-question-selector-item d-flex align-center ma-1 py-0 px-6"
                      :class="[{ 'active': active }, 'ghx-question-selector-item-' + nkey]"
                      :size="questionItem.form?.button_size"
                      @click="onSelectedItem(item.label, active); toggle()">
                      <v-scroll-y-transition>
                        <div
                          class="ghx-option-item flex-grow-1 text-center">
                          {{ item.label }}
                        </div>
                      </v-scroll-y-transition>
                    </v-card>
                  </v-item>
                </div>
              </div>
            </v-item-group>
          </v-card-text>
          <v-card-text v-else class="text-center">
            <v-item-group :key="questionItem.form.meta + questionItem.form.type" active-class="primary">
              <div
                class="ghx-question-selector-wrapper">
                <v-card class="ghx-glass-background ghx-date-picker" flat width="200">
                  <v-text-field
                    v-model="year"
                    class="ghx-form-input mt-2"
                    label="Year"
                    :rules="[rules.year]"
                    outlined
                    @keyup.enter="$emit('on-hit-enter')"></v-text-field>
                </v-card>
              </div>
            </v-item-group>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { questionnaire } from '@/store/main'
  import Util from '@/helpers/Util'
  
  const YEAR_NUMBER_LENGTH = 4;

  export default {
    name: 'StepOne',
    setup() {
      const store = questionnaire()

      return {
        store
      }
    },
    props: {
      questionItem: {
        type: Object,
        default: () => {}
      },
      initializer: {
        type: Boolean,
        default: false
      },
      
      currentOrder: {
        type: Number,
        default: 0
      }
    },
    
    mounted() {
      this.hasSelectedItem = this.store?.getSelectedFlow[this.questionItem.form.meta] || this.store.getUserResponse[this.questionItem.form.meta] || null;
      
      if (this.hasSelectedItem) {
        if (this.questionItem.form.type === 'date')
          this.year = this.hasSelectedItem/* && Util.isValidDate(this.hasSelectedItem)*/ ? this.hasSelectedItem: null;
        else {
          this.hasSelectedItem = this.questionItem.form.options.find( item => item.label === this.hasSelectedItem ) ? this.hasSelectedItem: null;;
          this.selectedItem = this.hasSelectedItem;
        }
      }
    },
    
    methods: {
      onSelectedItem (data, isActive) {
        this.hasSelectedItem = null;
        this.selectedItem = isActive ? null : data
      }
    },
    
    watch: {
      'selectedItem': {
        handler (val) {
          if (val) {
            var value = this.questionItem.form.type === 'date' ? this.year: val;
            var resource = {
              selected: value,
              resource: this.questionItem
            };

            this.$emit('on-item-selected', resource)
            
            if (this.initializer) 
              this.$emit('initialized', value)
            
            this.store.setSelectedFlow({key: this.questionItem.form.meta, value: value});
          } else 
            this.$emit('on-item-emptied')
        },
        deep: true
      },
      
      year (val) {
        var d = new Date();
        this.selectedItem = val.length === YEAR_NUMBER_LENGTH && val <= d.getFullYear() && val >= 1900 ? true: false;
      },
      
      /*dob_menu (val) {
        val && setTimeout(() => (this.activePicker = 'YEAR'))
      },*/
      
      initializer (val) {
        if (val)
          this.$emit('initialized', this.selectedItem)
      },
      
      currentOrder (val) {
        if (val === this.questionItem.order && this.selectedItem) {
          
          var resource = {
            selected: this.selectedItem,
            resource: this.questionItem
          };
          
          this.$emit('on-item-selected', resource)
        }
      }
    },
    
    data() {
      return {
        el: 1,
        //dob_menu: false,
        selectedItem: null,
        //activePicker: null,
        year: '',
        hasSelectedItem: null,
        
        rules: {
          year: value => {
            var x = value.replace(/\D/g, '').match(/(\d{0,4})/);
            this.year = x[1];
            var d = new Date();
            
            return this.year.length === YEAR_NUMBER_LENGTH && this.year <= d.getFullYear() && this.year >= 1900 ? true : 'Invalid Year.'
          }
        }
      }
    }
  }
</script>

