<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col 
        cols="12" 
        class="col-lg-12 col-sm-12"
      >
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p class="ghx-stepper-content__question text--primary" v-html="questionItem.text">
            </p>
          </v-card-text>
          <v-card-text class="text-center">

            <v-item-group :key="questionItem.form.meta + questionItem.form.type" active-class="primary" :value="hasSelectedItem">
              <div class="ghx-question-selector-wrapper">
                <div
                  v-for="(item, nkey) in questionItem?.form.options"
                  :key="nkey"
                  cols="12"
                  md="4"
                  class="ghx-question-selector">
<!--item.label === store?.getSelectedFlow?.label-->
                  <v-item v-slot="{ active, toggle }" :value="item.label">
                    <v-card
                      class="ghx-question-selector-item d-flex align-center ma-1 py-0 px-6"
                      :class="{ 'active':  active }"
                      :size="questionItem.form?.button_size"
                      @click="onSelectedItem(item.label, active); toggle()">
                      <v-scroll-y-transition>
                        <div
                          class="ghx-option-item flex-grow-1 text-center">
                          {{ item.label }}
                        </div>
                      </v-scroll-y-transition>
                    </v-card>
                  </v-item>
                </div>
              </div>
            </v-item-group>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

  import { questionnaire } from '@/store/main'
  import Util from '@/helpers/Util'

  export default {
    name: 'StepOneGeneral',
    
    setup() {
      const store = questionnaire()

      return {
        store
      }
    },
    
    props: {
      questionItem: {
        type: Object,
        default: () => {}
      },
      
      initializer: {
        type: Boolean,
        default: false
      },
      
      currentOrder: {
        type: Number,
        default: 0
      }
    },
    
    mounted() {
      var temp = this.store?.getSelectedFlow[this.questionItem.form.meta] || this.store.getUserResponse[this.questionItem.form.meta] || null;
      
      this.hasSelectedItem = this.questionItem.form.options.find( item => item.label === temp ) ? temp: null;
      
      if (this.hasSelectedItem)
        this.selectedItem = this.hasSelectedItem;
    },
    
    methods: {
      onSelectedItem (data, isActive) {
        this.hasSelectedItem = null;
        this.selectedItem = isActive ? null : data
      }
    },
    
    watch: {
      'selectedItem': {
        handler (val) {
          //this.hasSelectedItem = null;
          if (val) {
            //var resource = Util.getResourceVal(val, this.questionItem, this.questionItem.form.type);
            var resource = {
              selected: val,
              resource: this.questionItem
            };

            this.$emit('on-item-selected', resource)
            
            if (this.initializer) 
              this.$emit('initialized', val)
            
            this.store.setSelectedFlow({key: this.questionItem.form.meta, value: val});
          } else 
            this.$emit('on-item-emptied')
        },
        deep: true
      },
      
      initializer (val) {
        if (val)
          this.$emit('initialized', this.selectedItem)
      },
      
      currentOrder (val) {
        if (val === this.questionItem.order && this.selectedItem) {
          //var resource = Util.getResourceVal(this.selectedItem, this.questionItem);
          var resource = {
            selected: this.selectedItem,
            resource: this.questionItem
          };
          this.$emit('on-item-selected', resource)
        }
      }
    },
    data() {
      return {
        el: 1,
        selectedItem: null,
        hasSelectedItem: null
      }
    }
  }
</script>

